import { BaseObject } from "../BaseObject";
import { ObjectId } from "../ObjectId";
import { HCFiler } from "../filer/HCFiler";
import { HistoricManager } from "../history/HistoricManager";

/**
 * 
 */
export class BaseDatabase {
  /**id  */
  protected idIndex = 1;
  /**id */
  protected idMap = new Map<number, ObjectId>();
  HistoryManager: HistoricManager;
  constructor(protected preview = false) {
    this.HistoryManager = new HistoricManager()._SetOwnerDatabase(this);
    //100id
    this.idIndex = 100;
  }
  get AllObjects() {
    const objects: BaseObject[] = [];
    for (const [id, objectId] of this.idMap) {
      if (id < 100) continue;
      objects.push(objectId.Object);
    }
    return objects;
  }
  /**id */
  allocateId() {
    return this.getObjectId(this.idIndex++);
  }
  getObjectId(index: number) {
    //id 1,0id
    if (index === 0) return undefined;

    let id = this.idMap.get(index);
    if (id) return id;

    id = new ObjectId(index);
    this.idMap.set(index, id);

    return id;
  }
  /**
   * id
   * @param id
   * @returns 
   */
  findObjectById(id: number) {
    return this.idMap.get(id)?.Object;
  }
  /**
   * 
   * @param name
   * @param isLike  
   * @param filter true
   * @returns BaseObject[] 
   * @example app.Database.findObjectByName("box",true,(o)=>!(o intanceof GUI)) boxGUI
   */
  findObjectByName(
    name: string,
    isLike = true,
    filter?: (o: BaseObject) => boolean
  ): BaseObject[] {
    const objects: BaseObject[] = [];
    if (!filter) filter = (o) => false;
    for (const [, oid] of this.idMap) {
      if (oid.isErase) continue;
      if (!oid.Object?.Name || filter(oid.Object)) continue;
      if (isLike) {
        if (oid.Object.Name.includes(name)) {
          objects.push(oid.Object);
        }
      } else {
        if (oid.Object.Name === name) {
          objects.push(oid.Object);
        }
      }
    }
    return objects;
  }
  /** */
  reset() {}
  /** */
  writeFile(filer?: HCFiler) {
    return filer;
  }
  /** */
  readFile(filer: HCFiler) {
    return this;
  }
  /** */
  destory() {}
}
