import { BaseObject } from "./BaseObject";

/**
 * , ,
 */
export class HCFactory {
  private constructor() {
    //
  }
  private objectNameMap = new Map<string, any>();
  private static factory = new HCFactory();
  static RegisterObject(C: typeof BaseObject) {
    const key = C.type;
    if (!key) {
      throw new Error(C.name + "");
    }
    if (!this.factory.objectNameMap.has(key)) {
      this.factory.objectNameMap.set(key, C);
    } else {
      console.warn("" + key);
    }
  }
  static RegisterObjectAlias(C: any, name: string) {
    this.factory.objectNameMap.set(name, C);
  }
  static CreateObject<T = BaseObject>(name: string, option?: any): T {
    let C = this.factory.objectNameMap.get(name);
    if (C) return new C(option);
  }
  static isRegister(type: string) {
    return this.factory.objectNameMap.has(type);
  }
  static RegisterComponent(C: typeof BaseObject) {
    this.factory.objectNameMap.set(C.type, C);
  }
  static RemoveComponent(type: string) {
    this.factory.objectNameMap.delete(type);
  }
}

// (@Factory),
export function Factory(target: any) {
  HCFactory.RegisterObject(target);
}
