import { IVector3 } from "@hc/core";
import { IPointerEvent } from "../types";

//.
export interface EditorService {
  //
  isReady: boolean;
  //,. ,  .
  do: (e: IPointerEvent) => Promise<boolean> | boolean;
  cancel: () => void;
}

export enum ServiceStatus {
  Ok = 1,
  Cancel = 0,
  Error = -1,
  KeyWord = 2,
}

export enum EditorStatus {
  None = 0,
  /** */
  GetPoint = 0b000001,
  /** */
  Select = 0b000010,
  /** */
  Drag = 0b000100,
  All = 0b111111,
}

export interface ServiceFaildResult {
  status: ServiceStatus.Error | ServiceStatus.Cancel;
}

export type ServiceSuccessResul<T extends Record<string, any>> = {
  [K in keyof T]: T[K];
} & {
  status: ServiceStatus.Ok | ServiceStatus.KeyWord;
};

export type ServiceResult<T extends Record<string, any> = Record<string, any>> =
  ServiceFaildResult | ServiceSuccessResul<T>;

export type TKeyWord = {
  key: string;
  label: string;
};

/** */
export interface ServiceOption {
  /***/
  hasAuxiliaryLine?: boolean;
  /** */
  basePoint?: IVector3;
  /** */
  getPointCallback?: (p: IVector3) => void;
  keywordList?: TKeyWord[];
  msg?: string;
}
