import { BaseObject } from "./BaseObject";

/*
Id,.
id,.

ObjectId Database(dbid,id)
*/
export class ObjectId<T extends BaseObject = BaseObject> {
  private index: number;
  private object?: T;

  //,id, Create .
  constructor(index = -1, obj?: T) {
    this.index = index;
    this.object = obj;
  }

  get isErase(): boolean {
    return !this.object || this.object.IsErase;
  }
  set Object(v: T | undefined) {
    this.object = v;
  }
  get Object(): T | undefined {
    return this.object;
  }
  get Index(): number {
    return this.index;
  }
  set Index(index: number) {
    this.index = index;
  }
  toJSON() {
    return this.index;
  }
}
