import { BaseObject, IVector2 } from "@hc/core";

import {
  Mesh,
  Observable,
  PointerEventTypes,
  PointerInfo,
  TransformNode,
} from "../babylon";
import { BaseDom } from "../core/BaseDom";
import { Particle } from "../particleSystem/Particle";
import { GroupRecord } from "../records";
import { IPointerEvent, SelectType } from "../types";
import { getRoot, removeOne } from "../utils";
import { AppEvent } from "./../core/AppEvent";
import { Entity } from "./../entitys/Entity";
import { BaseGui } from "./../guis/Gui";
import { SelectMarquee } from "./../ui";
import { Viewer } from "./../viewer/Viewer";
import type { Editor } from "./Editor";
import { EditorService, EditorStatus } from "./interface";
import { SelectBox } from "./select/SelectBox";
import { SelectSet } from "./select/SelectSet";

export interface SelectEventDetail {
  type: "select";
  meshs: Array<Mesh | TransformNode>;
  entitys: BaseObject[];
  isUserInput: boolean;
}

export class SelectControl implements EditorService {
  selectMarquee: SelectMarquee;
  selectSet = new SelectSet();
  private doing = false;
  private selectObserable = new Observable<SelectEventDetail>();
  private _SelectType: SelectType = SelectType.None;
  private _selectGui: BaseGui;
  private _selectDOM: BaseDom;
  private _selectParticle: Particle;
  private _selectGroupRecord: GroupRecord;
  constructor(private _viewer: Viewer, private editor: Editor) {
    this.selectMarquee = new SelectMarquee(_viewer.canvas.parentElement);
    this._registerEvent();
  }

  get isReady() {
    return (
      this.editor.Status === EditorStatus.None ||
      !!(this.editor.Status & EditorStatus.Select)
    );
  }

  set SelectType(type: SelectType) {
    this._SelectType = type;
    this.selectMarquee.selectType = type;
  }
  set SelectEntitys(ens: Entity[]) {
    this.setSelectEntitys(ens);
  }
  do(event: IPointerEvent) {
    if (!this.isReady) return false;
    this.onPointerDown(event);
    return true;
  }
  cancel() {
    this.reset();
    this.clearSelect(true);
  }
  reset() {
    this.doing = false;
    this.selectMarquee.hide();
    this.editor.Status &= ~EditorStatus.Select;
  }

  /**
   * 
   *
   * @param ens 
   * @param isUserInput 
   */
  setSelectEntitys(ens: Entity[], isUserInput = true) {
    this.clearSelect();
    const s = new SelectSet();
    s.addEns(ens);
    this.selectSet = s;
    this.update(isUserInput);
  }

  get SelectMeshs() {
    if (this._selectGui || this._selectDOM) return [];
    return this.selectSet.SelectMeshs;
  }
  get SelectObjects() {
    if (this._selectGui) return [this._selectGui];
    if (this._selectDOM) return [this._selectDOM];
    if (this._selectParticle) return [this._selectParticle];
    if (this._selectGroupRecord) return [this._selectGroupRecord];
    return this.selectSet.SelectEntitys;
  }

  onPointerDown(pointerInfo: IPointerEvent) {
    if (this.SelectGui?.Draging) return;
    if (pointerInfo.event.button === 0) {
      const oldMeshs = this.SelectMeshs ?? [];
      this.clearSelect();
      if (pointerInfo.pickInfo.pickedMesh) {
        const root = getRoot(pointerInfo.pickInfo.pickedMesh) as Mesh;
        if (root.metadata?.Entity) {
          if (pointerInfo.event.ctrlKey || pointerInfo.event.metaKey) {
            if (oldMeshs.includes(root)) {
              removeOne(oldMeshs, root);
              this.selectSet.add(oldMeshs);
            } else {
              this.selectSet.add([root, ...oldMeshs]);
            }
          } else {
            this.selectSet.add([root]);
          }
          this.update();
          return;
        }
      }
      this.doing = true;
      this.editor.Status = EditorStatus.Select;
      this.selectMarquee.show();
      this.selectMarquee.setStart(
        this._viewer.Scene.pointerX,
        this._viewer.Scene.pointerY
      );
      this.selectMarquee.setEnd(
        this._viewer.Scene.pointerX,
        this._viewer.Scene.pointerY
      );
      this.selectMarquee.update();
    }
  }
  onPointerMove(pointerInfo: PointerInfo) {
    if (!this.doing) return;
    if (this.SelectGui?.Draging) return;

    this.selectMarquee.setEnd(
      this._viewer.Scene.pointerX,
      this._viewer.Scene.pointerY
    );

    this._SelectType =
      this.selectMarquee.end.x > this.selectMarquee.start.x
        ? SelectType.W
        : SelectType.C;
  }
  onPointerUp(pointerInfo: PointerInfo) {
    if (pointerInfo.event.button === 0 && this.doing) {
      this.selectByMarquee();
      this.reset();
    }
  }

  clearSelect(isUpdate = false) {
    if (this._selectGui) {
      this._selectGui.IsSelect = false;
      this._selectGui = null;
    }
    if (this._selectParticle) {
      this._selectParticle.IsSelect = false;
      this._selectParticle = null;
    }
    if (this._selectDOM) {
      this._selectDOM.IsSelect = false;
      this._selectDOM = null;
    }
    if (this._selectGroupRecord) {
      this._selectGroupRecord.IsSelect = false;
      this._selectGroupRecord = null;
    }

    this._viewer.highlightManager.clear();
    this.selectSet.clear();
    if (isUpdate) this.update();
  }
  on(callback: (data: SelectEventDetail) => void) {
    const f = this.selectObserable.add(callback);
    return () => {
      this.selectObserable.remove(f);
    };
  }

  onSelect(callback: (data: SelectEventDetail) => void): () => void {
    const ob = this.selectObserable.add(callback);

    return () => {
      this.selectObserable.remove(ob);
    };
  }

  update(isUserInput = true) {
    this.selectSet.SelectEntitys.forEach((e) => (e.IsSelect = true));
    this._viewer.highlightManager.addMeshs(this.selectSet.SelectMeshs);

    this.selectObserable.notifyObservers({
      type: "select",
      meshs: this.SelectMeshs,
      entitys: this.SelectObjects,
      isUserInput,
    });
  }
  get SelectGroupRecord() {
    return this._selectGroupRecord;
  }
  set SelectGroupRecord(v: GroupRecord) {
    this.clearSelect();
    this._selectGroupRecord = v;
    v.IsSelect = true;
    this.update();
  }
  get SelectParticle() {
    return this._selectParticle;
  }
  set SelectParticle(v: Particle) {
    this.clearSelect();
    this._selectParticle = v;
    v.IsSelect = true;
    this.update();
  }
  get SelectGui() {
    return this._selectGui;
  }
  set SelectGui(v: BaseGui) {
    this.clearSelect();
    this._selectGui = v;
    v.IsSelect = true;
    this.update();
  }
  get SelectDOM() {
    return this._selectDOM;
  }
  set SelectDOM(v: BaseDom) {
    this.clearSelect();
    this._selectDOM = v;
    v.IsSelect = true;
    this.update();
  }
  private selectByMarquee() {
    const selectBox = new SelectBox(
      this._viewer,
      this.selectMarquee.start,
      this.selectMarquee.end
    );
    selectBox._SelectType = this._SelectType;
    selectBox.select();
    this.selectSet.add(selectBox.SelectMeshs);
    this.update();
  }
  private _registerEvent() {
    this._viewer.Scene.onPointerObservable.add((pointerInfo: PointerInfo) => {
      switch (pointerInfo.type) {
        case PointerEventTypes.POINTERDOWN:
          break;
        case PointerEventTypes.POINTERUP:
          if (this.SelectGui?.Draging) {
            this.SelectGui.endDrag({
              x: pointerInfo.event.offsetX,
              y: pointerInfo.event.offsetY,
            });
          } else {
            this.onPointerUp(pointerInfo);
          }
          break;
        case PointerEventTypes.POINTERMOVE:
          if (this.SelectGui?.Draging) {
            this.SelectGui.drag({
              x: pointerInfo.event.offsetX,
              y: pointerInfo.event.offsetY,
            });
          } else {
            this.onPointerMove(pointerInfo);
          }
          break;
      }
    });
    this._viewer.canvas.parentElement.addEventListener("mousemove", (event) => {
      if (this.SelectDOM?.Draging) {
        this.SelectDOM.move(event.clientX, event.clientY);
      }
    });
    AppEvent.on("pointerDownGui", (event) => {
      this.SelectGui = event.target as BaseGui;
      this.SelectGui.startDrag(event.point);
    });
    document.addEventListener("mouseup", () => {
      if (this.SelectDOM?.Draging) {
        this.SelectDOM.end();
      }
    });
  }
}
